import React, { useState } from 'react';
import BasePage from 'components/BasePage';
import ButtonGroup from 'components/ButtonGroup';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
  FlexDiv,
  Img,
  ConversationBox,
} from 'components/BaseStyles';
import HelpBox from 'components/HelpBox';
import MessyImg from 'assets/images/messy.gif';
import ChatBot from 'assets/images/chat_robot.png';
import ChatBini from 'assets/images/chat_bini.png';
import ChatUI from 'components/ChatUI';

const Purpose: React.FC = () => {
  const [isOpenNextPage, setOpenNextPage] = useState(false);
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>  </ContentTitle>
          <ExplainBox>
                 ? <br />
                   
            !
          </ExplainBox>
          <Content>
            <FlexDiv>
              <Img src={MessyImg} alt="" />
              <ConversationBox>
                <ChatUI
                  users={[
                    { userId: 0, imgSrc: ChatBini },
                    { userId: 1, imgSrc: ChatBot, isFirstPersonView: true },
                  ]}
                  messages={[
                    {
                      id: 0,
                      userId: 0,
                      text:
                        '       ?',
                    },
                    {
                      id: 1,
                      userId: 1,
                      text:
                        '             ?',
                    },
                    {
                      id: 2,
                      userId: 1,
                      text:
                        '        ,                 !',
                    },
                  ]}
                  animation
                  setOpenNextPage={setOpenNextPage}
                />
              </ConversationBox>
            </FlexDiv>
          </Content>
          <HelpBox title="  " content="">
            <div>      !</div>
            <div>    </div>
            <div>     </div>
            <div>  .</div>
          </HelpBox>
        </DivBase>
        <ButtonGroup isOpenNextPage={isOpenNextPage} />
      </Container>
    </BasePage>
  );
};

export default Purpose;
