import React from 'react';
import ButtonGroup from 'components/ButtonGroup';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
  PointText,
} from 'components/BaseStyles';
import styled, { css } from 'styled-components';
import ReadOnlyBasket from 'components/ReadOnlyBasket';
import { pointViews, polygonsGroupByCluster, scores } from 'App/datas';
import { Theme } from 'styles/theme';
import { ColorType } from 'styles/colors';

const StyledContent = styled(Content)`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 60%;
  height: fit-content;
  margin: auto;
  margin-top: 7rem;
`;

const Question = styled.div`
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
`;
const Txt = styled.div`
  font-size: 1.1rem;
  height: 2rem;
`;

const Highlighting = styled.span`
  ${({ theme }) => css`
    color: ${(theme as Theme).palette[ColorType.Blue]};
  `}
`;

const AnalyticsProgress: React.FC = () => {
  const { groupScores, pointSum, score } = scores[2];
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>  </ContentTitle>
          <ExplainBox>
                    .
            <br />
              , ,        ?
          </ExplainBox>
          <StyledContent>
            {pointViews.map((pointView) => {
              const maxLengthPolygonIds = groupScores[pointView.groupBy];
              return (
                <div key={pointView.title}>
                  <Question>
                     <PointText>{pointView.title}</PointText>  {' '}
                    <Highlighting>{maxLengthPolygonIds.length}</Highlighting>
                  </Question>
                  <ReadOnlyBasket
                    group={polygonsGroupByCluster[2]}
                    showBorders={maxLengthPolygonIds}
                  />
                </div>
              );
            })}
          </StyledContent>
          <StyledContent
            style={{
              background: '#FAFAFA',
              marginTop: '1rem',
              fontSize: '1rem',
              padding: '1em',
              textAlign: 'center',
              display: 'block',
            }}
          >
            <Txt>
                  {groupScores.size.length},  {' '}
              {groupScores.color.length},  {groupScores.type.length}
              .   {pointSum}() .
            </Txt>
            <Txt>
                     {' '}
              {polygonsGroupByCluster[2].length}  ,
            </Txt>
            <Txt>
                     {score.toFixed(2)} 
              !
            </Txt>
          </StyledContent>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default AnalyticsProgress;
