import React from 'react';
import ButtonGroup from 'components/ButtonGroup';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
  PointText,
} from 'components/BaseStyles';
import styled from 'styled-components';
import ReadOnlyBasket from 'components/ReadOnlyBasket';
import { polygonsGroupByCluster } from 'App/datas';

const StyledContent = styled(Content)`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 60%;
  height: fit-content;
  margin: auto;
  margin-top: 8rem;
`;

const Question = styled.div`
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
`;

const pointViews = [{ title: '' }, { title: '' }, { title: '' }];

const AnalyticsProgress: React.FC = () => {
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>  </ContentTitle>
          <ExplainBox>
               
            <br />
                  !
          </ExplainBox>
          <StyledContent>
            {pointViews.map((pointView) => (
              <div key={pointView.title}>
                <Question>
                   <PointText>{pointView.title}</PointText>   ?
                </Question>
                <ReadOnlyBasket group={polygonsGroupByCluster[0]} />
              </div>
            ))}
          </StyledContent>
        </DivBase>
        <ButtonGroup buttonText=" " />
      </Container>
    </BasePage>
  );
};

export default AnalyticsProgress;
