import React from 'react';
import styled from 'styled-components';
import PointText from 'components/PointText';
import ButtonGroup from 'components/ButtonGroup';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import DimensionImg from 'assets/images/dimension.gif';

const Img = styled.img`
  margin-top: 2.5rem;
`;

const ExperienceDone: React.FC = () => {
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>   !</ContentTitle>
          <ExplainBox>
            <div>
                !     ,
              <br />
                  {' '}
              <PointText></PointText>
               . <br />
                    .
            </div>
          </ExplainBox>
          <Content>
            <Img src={DimensionImg} alt="" />
          </Content>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default ExperienceDone;
