import React, { useContext } from 'react';
import styled from 'styled-components';
import { FaHome as Home, FaArrowLeft as Back } from 'react-icons/fa';
import { MdReplay } from 'react-icons/md';
import { FromRight } from '../styles/animations';
import AppContext from '../App/context';
import ImageButton from './ImageButton';

const Container = styled.div`
  width: 100vw;
  height: 100vh;
  animation: ${FromRight} 0.4s ease-out both;
  position: relative;
`;

const ButtonTopLeft = styled.div`
  position: absolute;
  top: 1.5rem;
  left: 1.5rem;
`;

const ButtonTopRight = styled.div`
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  display: flex;
  justify-content: flex-end;

  button + button {
    margin-left: 1rem;
  }
`;

const Origin = styled.div`
  position: absolute;
  right: 1rem;
  bottom: 1rem;
  font-size: 0.6rem;
`;
type BasePageProps = {
  cover?: boolean;
};

const BasePage: React.FC<BasePageProps> = ({ children, cover }) => {
  const { goToHome, goToPrev, closeTab } = useContext(AppContext);
  const isClosableTab = window.opener !== null;
  return (
    <Container>
      {children}
      {!cover && (
        <ButtonTopLeft>
          <ImageButton onClick={goToPrev}>
            <Back />
          </ImageButton>
        </ButtonTopLeft>
      )}
      <ButtonTopRight>
        {!cover && (
          <ImageButton onClick={goToHome}>
            <MdReplay />
          </ImageButton>
        )}
        {isClosableTab && (
          <ImageButton onClick={closeTab}>
            <Home />
          </ImageButton>
        )}
      </ButtonTopRight>
      <Origin> 2021. 05. DataDriven. All rights reserved.</Origin>
    </Container>
  );
};

export default BasePage;
