import React from 'react';
import ButtonGroup from 'components/ButtonGroup';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import DiceImg from 'assets/images/dice.gif';

const General: React.FC = () => {
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>   </ContentTitle>
          <ExplainBox>
            , ,        ,
            <br />       ?
          </ExplainBox>
          <Content>
            <img style={{ height: '100%' }} src={DiceImg} alt="" />
          </Content>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default General;
